<template>
  <div class="nav-rel">
      <div class="mobile-navigation">
        <button class="mobile-hd-opener" @click="showLeft">
        <i class="fa fa-bars"></i>
        </button>
        <div class="mobile-logo">
        <a href="/">
            <img src="../assets/img/logo.png" />
        </a>
        </div>
        <button class="mobile-hd-opener2" @click="showRight">
        <i class="fa fa-user-o"></i>
        </button>
    </div>
    <div class="popWrap">
        <RightPop v-if="isRight" @func="popHide"></RightPop>
        <LeftPop v-if="isLeft" @func="popHide"></LeftPop>
    </div>
  </div>
</template>

<script>
import "font-awesome/css/font-awesome.min.css";
import RightPop from './RightPop'
import LeftPop from './LeftPop'

export default {
  name: "mobileNav",
  data() {
      return {
          isLeft: false,
          isRight: false
      }
  },
  components: {
      RightPop,
      LeftPop
  },
  methods: {
      showLeft() {
          this.isLeft = !this.isLeft;
          this.isRight = false;
      },
      showRight() {
          this.isRight = !this.isRight;
          this.isLeft = false;
      },
      popHide(isShow) {
          this.isLeft = isShow;
          this.isRight = isShow;
      }
  }
};
</script>

<style scoped>
.nav-rel {
    position: relative;
    height: 50px;
    width: 100%;
    /* background-color: red; */
}

.popWrap {
    position: absolute;
    height: 100%;
    z-index: 99999;
}

.mobile-navigation {
    position: absolute;
    z-index: 100;
  height: 50px;
  width: 100%;
  background-color: #333333;
  vertical-align: middle;
}

.mobile-hd-opener {
    cursor: pointer;
  position: absolute;
  top: 10px;
  left: 10px;
  padding: 0 10px;
  height: 30px;
  width: 30px;
  border: 0;
  background-size: 70%;
  color: #fff;
  background: 0 0;
  font-size: 16px;
}
.mobile-hd-opener2 {
    cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 0 10px;
  height: 30px;
  width: 30px;
  border: 0;
  background-size: 70%;
  color: #fff;
  background: 0 0;
  font-size: 16px;
}

.mobile-logo {
  text-align: center;
  height: 50px;
  padding: 5px 0;
}
.mobile-logo img {
  width: 100px;
  height: 40px;
}
</style>