<template>
    <div class="container">
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="item in swiperData" :key="item.index">
                    <span class="serial-number">{{item.rank}}.</span>
                    <a :href="`https://search.naver.com/search.naver?sm=top_hty&fbm=1&ie=utf8&query=${item.keyword}`" target="_blank" class="text">
                        <span>{{item.keyword}}</span>
                    </a>
                </div>
            </div>
            <a @click="show()" class="link">
                <i class="fa fa-chevron-down"></i>
            </a>
        </div>
        <div style="width: 100%">
            <div class="list" ref="list" v-if="isShow">
            <ul>
                <li class="list-item" v-for="item in swiperData" :key="item.index">
                    <span class="serial-number">{{item.rank}}.</span>
                    <a :href="`https://search.naver.com/search.naver?sm=top_hty&fbm=1&ie=utf8&query=${item.keyword}`" target="_blank" class="text">
                        <span>{{item.keyword}}</span>
                    </a>
                </li>
                <li class="cancle-btn">
                    <i class="fa fa-times" @click="show()"></i>
                </li>
            </ul>
        </div>
        </div>
    </div>
</template>
<script>
// import 'swiper/dist/css/swiper.min.css'
// import 'swiper/dist/js/swiper'
import "font-awesome/css/font-awesome.min.css";
import axios from 'axios';
import Swiper from 'swiper';
import "swiper/css/swiper.min.css";
import "swiper/js/swiper";

export default {
    name: 'MobileSwiper',
    data() {
        return {
            isShow: false,
            swiperData: {},
            flag: false
        }
    },
    methods: {
        show() {
            this.isShow = !this.isShow;
            // var dis = this.$refs.list.style.display;
            // if (dis == 'none') {
            //     this.$refs.list.style.display = 'block';
            // } else {
            //     this.$refs.list.style.display = 'none';
            // }
        }
    },
    mounted() {
        axios.get('https://api.signal.bz/news/realtime').then(data => {
            this.swiperData = data.data['top10'];
        })
        
        
    },
    updated() {
        if(!this.flag) {
            this.$nextTick(()=>{
            var  mySwiper =  new Swiper ('.swiper-container', {
                loop: true,
                direction : 'vertical',
                touchRatio : 0,
                autoplay: true
            })
        })
        }
        this.flag = true;
    }
}
</script>
<style scoped>

    .container {
        width: 100%;
        background-color: #ffffff;
    }

    .swiper-container {
        width: 100%;
        height: 50px;
        border: solid 1px #eeeeee;
        box-sizing: border-box;
    }
    .serial-number {
        color: #dc1400;
        font: 15px "noto sans kr";
        line-height: 50px;
        margin-left: 20px;
        font-weight: 700;
    }
    .text {
        text-decoration: none;
        color: #000000;
        font: 15px "noto sans kr";
        line-height: 50px;
        /* font-weight: 700; */
        margin-left: 15px;
    }
    .link {
        z-index: 4000;
        display: block;
        width: 50px;
        height: 50px;
        position: absolute;
        top: 0;
        right: 0;
        /* background-color: red; */
        color: #000;
        text-decoration: none;
        text-align: center;
    }
    .link i {
        cursor: pointer;
        line-height: 50px;
    }

    .list {
        display: block;
        position: absolute;
        width: 100%;
        margin: 0;
        padding: 0;
        /* left: 1%; */
        /* height: 100px; */
        background-color: white;
        border: 2px solid #f7f7f7;
        z-index: 999;
        outline: none;
        box-sizing: border-box;
    }

    .list-item {
        height: 30px;
    }
    ul {
        position: relative;
        padding-left: 10px;
        margin: 0;
    }
    li {
        padding: 0;
        margin: 0;
        list-style: none;
        list-style-position: inside;
    }
    .cancle-btn {
        padding-right: 22px;
        margin-top: 15px;
        margin-bottom: 15px;
        text-align: right;
    }
    .cancle-btn i {
        cursor: pointer;
    }
</style>