<template>
    <div class="slide-up">
        <button class="slide-btn" @click="slideUp">
            <i class="fa fa-chevron-up"></i>
        </button>
    </div>
</template>
<script>
import "font-awesome/css/font-awesome.min.css";

export default {
    name: "SlideUp",
    methods: {
        slideUp() {
            let top = document.documentElement.scrollTop || document.body.scrollTop
            // 实现滚动效果
            const timeTop = setInterval(() => {
            document.body.scrollTop = document.documentElement.scrollTop = top -= 50
                if (top <= 0) {
                    clearInterval(timeTop)
                }
            }, 10)
        }
    },
}
</script>
<style scoped>
    .slide-up {
        position: fixed;
        bottom: 10px;
        right: 10px;
    }
    .slide-btn {
        width: 35px;
        height: 35px;
        text-align: center;
        background: 0;
        cursor: pointer;
    }
</style>