<template>
<div>
  <div class="pcHome2" v-if="isPc">
    <pcArticle2></pcArticle2>
    <div class="top" @click="jump()">
      <i class="fa fa-chevron-up" aria-hidden="true"></i>
    </div>
</div>
<MobileHome2 v-if="isMobile" />
</div>
</template>

<script>
import pcArticle2 from '../components/pcArticle2.vue'
import MobileHome2 from '../components/MobileHome2.vue'
export default {
    name:'pcHome2',
    components:{
        pcArticle2,MobileHome2
    },
    data() {
      return {
          isMobile: false,
          isPc: true
      }
  },
  mounted() {
      this.isMobile = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i);
      this.isPc = !this.isMobile;
  },
    methods:{
      jump(){
        window.scroll({
          top:0,
          behavior:"smooth"
        })
      }
    }
}
 
</script>

<style scoped>
.pcHome2{
  background-color: #f9f9f9;
}
.top{
    width: 40px;
    height: 40px;
    border: 3px solid #999999;
    position: fixed;
    bottom: 25px;
    right: 25px;
    cursor: pointer;
    text-align: center;
    line-height: 32px;
    color: #999999;
}
.top:hover{
    background-color: #999999;
    color: white;
}
</style>