<template>
  <div>
        <MobileNav></MobileNav>
        <SearchBar></SearchBar>
        <MobileSwiper></MobileSwiper>
        <MobileAdvertis>
            <div class="ad10">
                <ul>
                    <li><a href=""  class="bg">网络卡通</a></li>
                    <li><a href=""  class="bg">成人</a></li>
                    <li><a href=""  class="bg">确认</a></li>
                    <li><a href=""  class="bg">戏剧</a></li>
                    <li><a href=""  class="bg">种子</a></li>
                </ul>
                <ul>
                    <li><a href=""  class="bg">运动的</a></li>
                    <li><a href=""  class="bg">奥派</a></li>
                    <li><a href="" class="bg">社区</a></li>
                    <li><a href="" class="bg">成人用品</a></li>
                    <li><a href="" class="bg">韩国人</a></li>
                </ul>
            </div>
        </MobileAdvertis>
         <div class="lastUlParent">
        <div class="lastUl">
          <ul>
              <li v-for="(item,i) in msgData" :key="i" @click="send(item.href)">
                  <!-- <a :href="item.href"> -->
                  <div>
                      <img :src="item.imgUrl" alt="">
                      <img :src="item.icon" alt="" class="xf xf1">
                  </div>
                  <p><span :style="{color:item.color}">{{item.title}}</span><span><i class="fa fa-heart"></i>268,169</span></p>
                  <!-- </a> -->
              </li>
          </ul>
      </div>
      </div>
      <h4><a href="">{{footerData.title}}</a></h4>
          <footer>
              <h3>{{footerData.message1+footerData.message2}}</h3>
              <p>{{footerData.message3}}</p>
          </footer>
  </div>
</template>

<script>
import MobileNav from './MobileNav';
import SearchBar from './SearchBar';
import MobileSwiper from './MobileSwiper';
import MobileAdvertis from './MobileAdvertis'
import axios from 'axios';
export default {
    name:'MobileHome2',
     components: {
      MobileNav,
      SearchBar,
      MobileSwiper,
      MobileAdvertis
    },
    data() {
        return {
            footerData: {},
            msgData:{},
        }
    },
   mounted() {
        axios.get('/api/footer.json').then(data => {
            this.footerData = data.data['脚部信息'];
        });
        var i=this.$route.query.count;
        var bg=document.getElementsByClassName("bg")
        bg[i-1].setAttribute('style', 'color: white !important;background-color:#dc1400');
        if(i==="1"){
             axios.get('/api/page2.json').then(data => {
            this.msgData = data.data['网络卡通'];
            this.text="网络卡通"
        })
        }else if(i==="2"){
             axios.get('/api/page22.json').then(data => {
            this.msgData = data.data['成人'];
            this.text="成人"
        })
        }else if(i==="3"){
             axios.get('/api/page23.json').then(data => {
            this.msgData = data.data['确认'];
            this.text="确认"
        })
        }else if(i==="4"){
             axios.get('/api/page24.json').then(data => {
            this.msgData = data.data['戏剧'];
            this.text="戏剧"
        })
        }else if(i==="5"){
             axios.get('/api/page25.json').then(data => {
            this.msgData = data.data['种子'];
            this.text="种子"
        })
        }else if(i==="6"){ 
            axios.get('/api/page26.json').then(data => {
            this.msgData = data.data['运动的'];
            this.text="运动的"
        })
        }else if(i==="7"){
             axios.get('/api/page27.json').then(data => {
            this.msgData = data.data['奥派'];
            this.text="奥派"
        })
        }else if(i==="8"){
            axios.get('/api/page28.json').then(data => {
            this.msgData = data.data['社区'];
            this.text="社区"
        })
        }else if(i==="9"){
            axios.get('/api/page29.json').then(data => {
            this.msgData = data.data['成人用品'];
            this.text="成人用品"
        })
        }else if(i==="10"){
             axios.get('/api/page210.json').then(data => {
            this.msgData = data.data['韩国人'];
            this.text="韩国人"
        })
        }
    },
    methods:{
        send(val){
            console.log(val);
             let routeData=this.$router.resolve({
               path:'/jump',
               query:{
                   name:'img',
                   address:val
               }
           })
           window.open(routeData.href,'_blank')
        }
    },
}
</script>

<style scoped>
.ad10 ul{
    display: flex;
    justify-content: space-evenly;
}
.ad10 ul li{
    width: 20%;
    height: 40px;
    line-height: 40px;
    border: 1px solid #efefef;
    text-align: center;
}
.ad10 ul li a{
    display: inline-block;
    width: 100%;
    height: 100%;
    color: black;
    font-size: 12px;
    font-weight: 900;
}
.ad10 ul li a:hover{
    color: #dc1400;
}
/*  */
.lastUlParent{
    background-color: #fff;
}
.lastUl ul{
    margin: 10px auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.lastUl ul li{
    width: 49%;
    height: 145px;
    margin-bottom: 10px;
    border: 1px solid #dddddd;
    position: relative;
    cursor: pointer;
}
.lastUl ul li div:first-of-type{
    height: 100px;
    border-bottom: 1px solid #eeeeee;
}
.lastUl ul li p{
    line-height: 45px;
    height: 45px;
    background-color: #eeeff5 ;
}
.lastUl ul li p span:first-of-type{
    margin-left: 10px;
    float: left;
    font-weight: 700;
    transform: scale(.7);
}
.lastUl ul li p span:last-of-type{
    float: right;
    margin-right: 10px;
    color: #ff8a00;
     transform: scale(.7);
}
.lastUl ul li p span i{
    margin-right: 5px;
    color: #f0001e;
}
.lastUl ul li div{
    line-height: 150px;
    text-align: center;
}
.lastUl ul li div img:first-of-type{
    width: 150px;
}
.xf{
    position: absolute;
    top: 0;
    left: 2px;
    width: 28px;
}
h4{
    text-align: center;
    height: 80px;
    line-height: 80px;
    border-top: 1px solid #eeeeee;
    border-bottom: 1px solid #eeeeee;
    background-color: #f9f9f9;
}
/*  */
h4 a{
    font-weight: 400;
    font-size: 12px;
    color: #9999a5 !important;
    background-color: transparent !important;
}
footer{
    text-align: center;
    height: 120px;
    background-color: #222222;
    border: 1px solid transparent;
}
footer h3{
    margin: 30px auto 0;
    font-weight: 400;
    color: white;
    font-size: 12px;
    width: 300px;
    font-weight: 900;
    font-family: noto sans kr;
    transform: scale(.9);
}
footer p{
    margin-top: 20px;
    font-size: 12px;
    color: #b3b3b3;
    transform: scale(0.8);
}
</style>