<template>
    <div class="listContainer">
        <a :href="listHeadData.href">
            <div class="title-wrap" @click="send">
                    <div class="title-img-wrap">
                        <img class="title-img" :src="listHeadData.icon">
                    </div>
                    <span>{{listHeadData.title}}</span>
                    <a href="#" class="icon">
                        <i class="fa fa-chevron-right"></i>
                    </a>
            </div>
        </a>
        <ul class="list-content" >
            <li v-for="item,i in listData" :key="i" @click='jump(item.href)'>
                <!-- <a :href="item.href"> -->
                    <img :src="item.icon">
                    <span :style="{color: item.color}">{{item.title}}</span>
                <!-- </a> -->
            </li>
        </ul>
    </div>
</template>
<script>
import "font-awesome/css/font-awesome.min.css";
import axios from 'axios';

export default {
    name: "ListItem",
    props: ['index'],
    data() {
        return {
            listData: {},
            listHeadData: {}
        }
    },
    mounted() {
        axios.get('/api/data.json').then(data=>{
            this.listData = data.data[`推荐${this.index}区`];
            this.listHeadData = data.data[`推荐${this.index}区头部信息`]
            // console.log(this.listData[0]['color']);
        })
    },
    methods:{
        send(){
            var i=0;
            if(this.index===1){
               i=1
           }else if(this.index===2){
               i=2
           }else if(this.index===3){
               i=3
           }else if(this.index===4){
               i=4
           }else if(this.index===5){
               i=5
           }else if(this.index===6){
               i=6
           }else if(this.index===7){
               i=7
           }else if(this.index===8){
               i=8
           }else if(this.index===9){
               i=9
           }else if(this.index===10){
               i=10
           }
            let routeData=this.$router.resolve({
               path:'/h2',
               query:{
                   count:i,
               }
           })
           window.open(routeData.href,'_blank')
        },
        jump(val){
             let routeData=this.$router.resolve({
               path:'/jump',
               query:{
                   name:'top10',
                   address:val
               }
           })
           window.open(routeData.href,'_blank')
        }
    }

}
</script>
<style scoped>
    .listContainer {
        width: 49.5%;
        display: inline-block;
        background-color: #ffffff;
        margin-bottom: 10px;
    }
    .title-wrap {
        height: 60px;
        padding-left: 10px;
        border: solid 1px #eeeeee;
    }
    .title-img-wrap {
        display: inline-block;
        height: 60px;
        vertical-align: middle;
    }
    .title-img {
        width: 20px;
        height: 20px;
        margin-top: 20px;
        line-height: 60px;
    }
    .title-wrap span {
        display: inline-block;
        font-size: 14px;
        color: #000000;
        height: 60px;
        line-height: 60px;
        font-weight: 700;
        margin-left: 10px;
    }
    ul {
        padding: 10px;
        margin: 0;
        border: solid 1px #eeeeee;
        margin-bottom: 10px;
    }
    ul:last-child {
        margin-bottom: 0;
    }
    li {
        font-size: 14px;
        font-weight: 700;
        height: 30px;
        list-style: none;
        list-style-position:inherit;
    }

    li img {
        margin-right: 10px;
        width: 20px;
        height: 20px;
        line-height: 30px;
        vertical-align: middle;
    }
    a {
        text-decoration: none;
    }
    .title-wrap {
        position: relative;
    }
    .icon {
        position: absolute;
        top: 22px;
        right: 10px;
        color: #999999;
        width: 20px;
        height: 20px;
    }
</style>