<template>
    <div class="container">
        <div class="btn-wrap">
            <button @click="hidepop">
                <i class="fa fa-times"></i>
            </button>
        </div>
        <div class="content">
            <a :href="loginLink">
                <span class="login">
                    <i class="fa fa-sign-in"></i>
                    <div>로그인</div>
                </span>
            </a>
            <a :href="signUpLink">
                <span class="signUp">
                    <i class="fa fa-user-plus"></i>
                    <div>회원가입</div>
                </span>
            </a>
        </div>
    </div>
</template>
<script>
import "font-awesome/css/font-awesome.min.css";
import axios from 'axios';

export default {
    name: "RightPop",
    data() {
        return {
            loginLink: '',
            signUpLink: ''
        }
    },
    methods: {
        hidepop () {
            this.$emit('func', false);
        }
    },
    mounted() {
        axios.get('/api/login.json').then(data=>{
            this.loginLink = data.data['登录链接'];
            this.signUpLink = data.data['报名链接'];
        })
    },
}
</script>
<style scoped>
    .container {
        width: 300px;
        height: 100%;
        position: fixed;
        background-color: #ffffff;
        right: 0;
        z-index: 9999;
        box-shadow: 0 0 5px rgb(55 55 5 / 40%);
        
    }
    .btn-wrap {
        width: 100%;
        background: #222222;
        height: 50px;
    }
    .content {
        padding: 10px;
    }
    .container button{
        position: absolute;
        cursor: pointer;
        background: 0 0;
        border: none;
        color: #fff;
        text-align: right;
        top: 17px;
        right: 10px;
        margin-bottom: 20px;
    }
    .content i {
        width: 20px;
        height: 20px;
        font-size: 20px;
    }
    .content a {
        text-decoration: none;
        color: #717171;
    }
    .login {
        margin-top: 20px;
        display: inline-block;
        width: 50%;
        text-align: center;
        border-right: 1px solid #ebedef;
        border-bottom: 1px solid #ebedef;
        box-sizing: border-box;
        padding: 15px;
    }
    .signUp {
        margin-top: 20px;
        display: inline-block;
        width: 50%;
        text-align: center;
        border-left: 1px solid #ebedef;
        border-bottom: 1px solid #ebedef;
        box-sizing: border-box;
        padding: 15px;
    }
    .content div {
        font-size: 13px;
        color: #717171;
        font-weight: 700;
        margin: 10px;
    }
</style>