<template>
    <div class="container">
        <button @click="hidepop">
            <i class="fa fa-times"></i>
        </button>
        <ul>
            <li v-for="item,i in popLeftData" :key="i" class="popItem">
                <a :href="item.href">{{item.title}}</a>
            </li>
        </ul>
    </div>
</template>
<script>
import "font-awesome/css/font-awesome.min.css";
import axios from 'axios';

export default {
    name: "LeftPop",
    data() {
        return {
            popLeftData: []
        }
    },
    methods: {
        hidepop () {
            this.$emit('func', false);
        }
    },
    mounted() {
        axios.get('/api/popLeft.json').then(data=>{
            this.popLeftData = data.data['菜单左弹出栏'];
        })
    },
}
</script>
<style scoped>
    .container {
        width: 300px;
        height: 100%;
        position: fixed;
        background-color: #262626;
        z-index: 9999;
        box-shadow: 0 0 5px rgb(55 55 5 / 40%);
    }
    .container button{
        position: absolute;
        cursor: pointer;
        background: 0 0;
        border: none;
        color: #fff;
        text-align: right;
        top: 10px;
        right: 10px;
        margin-bottom: 20px;
    }
    ul {
        padding: 0;
        padding-top: 20px;
    }
    .popItem {
        list-style: none;
        height: 40px;
        padding-left: 20px;
        line-height: 40px;
    }
    .popItem a {
        color: #fff;
        text-decoration: none;
        font-size: 14px;
    }
</style>