<template>
  <div class="pcArticle">
      <pcNav>
            <a href="#" class="bg">
                网络卡通
            </a>
           <a href="#" class="bg">
               成人
            </a>
            <a href="#" class="bg">
                确认
            </a>
            <a href="#" class="bg">
                戏剧
            </a>
            <a href="#" class="bg">
                种子
            </a>
            <a href="#" class="bg">
                运动的
            </a>
            <a href="#" class="bg">
                奥派
            </a>
            <a href="#" class="bg">
                社区
            </a>
            <a href="#" class="bg">
                成人用品
            </a>
            <a href="#" class="bg">
                韩国人
            </a>
      </pcNav>
      <nav>
          <img src="" alt="">{{text}}
      </nav>
      <div class="lastUlParent">
      <div class="lastUl">
          <ul>
              <li v-for="(item,i) in msgData" :key="i" @click="send(item.href)">
                  <!-- <a :href="item.href"> -->
                  <div>
                      <img :src="item.imgUrl" alt="">
                      <img :src="item.icon" alt="" class="xf xf1">
                  </div>
                  <p><span :style="{color:item.color}">{{item.title}}</span><span><i class="fa fa-heart"></i>268,169</span></p>
                  <!-- </a> -->
              </li>
          </ul>
      </div>
      </div>
      <h4><a href="">{{footerData.title}}</a></h4>
          <footer>
              <h3>{{footerData.message1+footerData.message2}}</h3>
              <p>{{footerData.message3}}</p>
          </footer>
  </div>
</template>

<script>
import pcNav from '../components/pcNav.vue'
import axios from 'axios';
export default {
    name:'pcArticle2',
    components:{
        pcNav
    },
     data() {
        return {
            footerData: {},
            msgData:{},
            text:''
        }
    },
    methods:{
        send(val){
            console.log(val);
             let routeData=this.$router.resolve({
               path:'/jump',
               query:{
                   name:'img',
                   address:val
               }
           })
           window.open(routeData.href,'_blank')
        }
    },
    mounted() {
        axios.get('/api/footer.json').then(data => {
            this.footerData = data.data['脚部信息'];
        });
        var i=this.$route.query.count;
        var bg=document.getElementsByClassName("bg")
        bg[i-1].setAttribute('style', 'color: white !important;background-color:#dc1400');
        if(i==="1"){
             axios.get('/api/page2.json').then(data => {
            this.msgData = data.data['网络卡通'];
            this.text="网络卡通"
        })
        }else if(i==="2"){
             axios.get('/api/page22.json').then(data => {
            this.msgData = data.data['成人'];
            this.text="成人"
        })
        }else if(i==="3"){
             axios.get('/api/page23.json').then(data => {
            this.msgData = data.data['确认'];
            this.text="确认"
        })
        }else if(i==="4"){
             axios.get('/api/page24.json').then(data => {
            this.msgData = data.data['戏剧'];
            this.text="戏剧"
        })
        }else if(i==="5"){
             axios.get('/api/page25.json').then(data => {
            this.msgData = data.data['种子'];
            this.text="种子"
        })
        }else if(i==="6"){ 
            axios.get('/api/page26.json').then(data => {
            this.msgData = data.data['运动的'];
            this.text="运动的"
        })
        }else if(i==="7"){
             axios.get('/api/page27.json').then(data => {
            this.msgData = data.data['奥派'];
            this.text="奥派"
        })
        }else if(i==="8"){
            axios.get('/api/page28.json').then(data => {
            this.msgData = data.data['社区'];
            this.text="社区"
        })
        }else if(i==="9"){
            axios.get('/api/page29.json').then(data => {
            this.msgData = data.data['成人用品'];
            this.text="成人用品"
        })
        }else if(i==="10"){
             axios.get('/api/page210.json').then(data => {
            this.msgData = data.data['韩国人'];
            this.text="韩国人"
        })
        }
    },
}
</script>

<style scoped>
.lastUlParent,nav{
    background-color: #fff;
}
a{
 width: 20% !important;
 text-align: center !important;
 color: black !important;
 border-right:1px solid #eeeeee;
}
a:last-of-type{
 border-right:none;
}
/* a:first-of-type{
    background-color: #dc1400;
    color: white !important;
} */
a:nth-of-type(n+2):hover{
    color: #dc1400 !important;
}
nav{
    width: 1200px;
    margin: 10px auto;
    border: 1px solid #eeeeee;
    padding: 20px 20px;
}
.lastUl ul{
    width: 1200px;
    margin: 10px auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.lastUl ul li{
    width: 19.5%;
    height: 180px;
    margin-bottom: 10px;
    border: 1px solid #dddddd;
    position: relative;
    cursor: pointer;
}
.lastUl ul li div:first-of-type{
    height: 120px;
    border-bottom: 1px solid #eeeeee;
}
.lastUl ul li p{
    line-height: 60px;
    height: 60px;
    background-color: #eeeff5 ;
}
.lastUl ul li p span:first-of-type{
    margin-left: 10px;
    float: left;
    font-weight: 700;
}
.lastUl ul li p span:last-of-type{
    float: right;
    margin-right: 10px;
    color: #ff8a00;
}
.lastUl ul li p span i{
    margin-right: 5px;
    color: #f0001e;
}
.lastUl ul li div{
    line-height: 120px;
    text-align: center;
}
.lastUl ul li div img:first-of-type{
    width: 80%;
}
.xf{
    position: absolute;
    top: 0;
    left: 2px;
    width: 12%;
}
h4{
    text-align: center;
    height: 80px;
    line-height: 80px;
    border-top: 1px solid #eeeeee;
    border-bottom: 1px solid #eeeeee;
    background-color: #f9f9f9;
}
.pcArticle h4 a{
    font-weight: 400;
    font-size: 12px;
    color: #9999a5 !important;
    background-color: transparent !important;
}
footer{
    text-align: center;
    height: 100px;

}
footer h3{
    font-size: 14px;
    margin-top: 50px;
    font-weight: 400;
}
footer p{
    margin-top: 20px;
    font-size: 12px;
    color: #b3b3b3;
}
.fa-chevron-right{
    color: #999999;
    float: right;
    margin-top: 25px;
    margin-right: 8px;
    cursor: pointer;
    font-size: 14px !important;
}
.pcArticle2-ul  .fa-chevron-right{
    margin-top: 5px;
    font-size: 12px !important;
}
.pcArticle2-ul a{
    padding-right: 66px;
}
</style>