        //        i=8;

        //    }else if(index===9){
        //        i=9;
        //    }
        //    let routeData=this.$router.resolve({
        //        path:'/jump',
        //        query:{
        //            count:i
        //        }
        //    })
        //    window.open(routeData.href,'_blank')
        // }
    },
    updated() {
        this.$nextTick(()=>{
            var  mySwiper=  new Swiper ('.swiper-container', {
            loop: true,
            direction : 'vertical',
            touchRatio : 0,
            autoplay: true
        })
    })
   
    },
    mounted(){
        axios.get('/api/ad.json').then(data=>{
            this.ad1 = data.data['第一和二个广告图片数据'];
            this.ad3 = data.data['第三个广告图片数据'];
        })
        axios.get('/api/form.json').then(data=>{
            this.SearchTar = data.data['提交目的链接'];
            this.SearchData = data.data['搜索引擎列表'];
        })
        axios.get('/api/login.json').then(data=>{
            this.loginLink = data.data['登录链接'];
            this.signUpLink = data.data['报名链接'];
            this.signAdd = data.data['在这里添加到收藏夹链接'];
            this.signTw = data.data['推特链接'];
            this.signNo= data.data['注意链接'];
            this.signAdV = data.data['广告/附属公司查询链接'];
        })
        axios.get('https://api.signal.bz/news/realtime').then(data => {
            this.swiperData = data.data['top10'];
        })
    var pcHome = document.getElementsByTagName("body")
    var h2 = document.getElementsByClassName("pcNav-section")
    var s=null;
    if(this.a){
        s=setInterval(function() {
        var w = pcHome[0].clientWidth;
        var a = w / 1496
        h2[0].style.stytransform = "scale(" + a + ")"
    })
    }else{
        clearInterval(s)
    }
        var text=document.getElementsByClassName("swiper-container ")
        var st=document.getElementsByClassName("static")
        text[0].onmouseover=function(){
            st[0].style.display="block"
        }
        st[0].onmouseover=function(){
            st[0].style.display="block"
        }
        st[0].onmouseout=function(){
            st[0].style.display="none"
        }
  }
}
</script>

