import Vue from 'vue'
import VueRouter from 'vue-router'
import pcHome from '../views/pcHome.vue'
import pcHome2 from '../views/pcHome2.vue'
import jump from '../views/jump.vue'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'pcHome',
    component: pcHome
}, {
    path: "/h2",
    name: 'pcHome2',
    component: pcHome2
}, {
    path: '/jump',
    name: 'jump',
    component: jump
}]

const router = new VueRouter({
    routes
})

export default router