<template>
<div>
    <div class="pcHome" v-if="isPc" >
    <pcNav :isPc="isPc"></pcNav>
    <pcArticle></pcArticle>
    <div class="top" @click="jump()">
      <i class="fa fa-chevron-up" aria-hidden="true"></i>
    </div>

</div>
    <MobileHome v-if="isMobile"/>

</div>
</template>

<script>
import pcNav from '../components/pcNav.vue'
import pcArticle from '../components/pcArticle.vue'
import MobileHome from '../components/MobileHome'

export default {
    name:'PcHome',
    components:{
        pcNav,pcArticle,MobileHome
    },
    data() {
      return {
          isMobile: false,
          isPc: true
      }
  },
  mounted() {
      this.isMobile = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i);
      this.isPc = !this.isMobile;
  },
    methods:{
      jump(){
        window.scroll({
          top:0,
          behavior:"smooth"
        })
      }
    }
}
 
</script>

<style scoped>
.top{
    width: 40px;
    height: 40px;
    border: 3px solid #999999;
    position: fixed;
    bottom: 25px;
    right: 25px;
    cursor: pointer;
    text-align: center;
    line-height: 32px;
    color: #999999;
}
.top:hover{
    background-color: #999999;
    color: white;
}

</style>