      <div class="main">
        <div class="circle"></div>
        <h1 id="second">{{count}}</h1>
        <p class="title">{{titleT}}</p>
        <p class="text">{{textT}}</p>
    </div>
 </div>
</template>

<script>
export default {
    name:'jump',
    data(){
        return{
            hrefLocal:'',
