            titleT:'안전운행중',
            textT:'크롬브라우저사용하세요，안전하게 목표주소까지 이동합니다'
        }
    },
    created: function() {
            // 每次进入界面时，先清除之前的所有定时器，然后启动新的定时器
            clearInterval(this.timer)
            this.timer = null
            this.setTimer(this.$route.query.count,this.$route.query.name,this.$route.query.address)
             function goBack() {
                window.history.go(-1);
            }
        },
        destroyed: function () {
            // 每次离开当前界面时，清除定时器
            clearInterval(this.timer)
            this.timer = null
        },
    methods:{
