    setTimer(val,name,address) {
         if(this.timer == null) {
                    var i=6;
                    this.count=i+"s"
                    this.timer = setInterval( () => {
                        i--;
                        this.count=i+"s"
                        if(i===1){
                            if(name==="top10"){
                                window.location.href = address
                            }else if(name==="img"){
                                window.location.href = address
                            }
                            else{
                                window.location.href = "http://www.baidu.com"
                             }
                        }
                    }, 1000)
                }
            }

    
    //  time() {
        
