    //         this.count=i + "s"
    //          if(i==1){
    //         //   window.location.href = count();
    //         //   console.log(this.hrefLocal);
    //         //  window.open();
    //          }
    //     }, 1000);

    //     function goBack() {
    //         window.history.go(-1);
