    //  }
    }
}
</script>

<style scoped>
        .main {
            position: relative;
            width: 100%;
            height: 722px;
            background: #000;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        
        .circle {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 200px;
            height: 200px;
            background-image: linear-gradient(0deg, rgb(47, 102, 255), rgb(153, 64, 255) 30%, rgb(238, 55, 255) 60%, rgb(255, 0, 76) 100%);
            border-radius: 50%;
            animation: rotate 1s linear infinite;
        }
        /* 模糊 */
        
        .circle::before {
            content: "";
            position: absolute;
            width: 200px;
            height: 200px;
            background-image: linear-gradient(0deg, rgb(47, 102, 255), rgb(153, 64, 255) 30%, rgb(238, 55, 255) 60%, rgb(255, 0, 76) 100%);
            border-radius: 50%;
            filter: blur(35px);
        }
        /* 黑圆 */
        
        .circle::after {
            content: "";
            position: absolute;
            width: 150px;
            height: 150px;
            background: #000;
            border-radius: 50%;
        }
        
        h1 {
            position: absolute;
            color: #fff;
            font-weight: bold;
        }
        
        .title {
            color: #FFFFFF;
            position: absolute;
            top: 10%;
            text-shadow: 0px 0 30px #fff;
            text-align: center;
            font-size: 2rem;
            font-weight: bold;
        }
        
        .text {
            color: #FFFFFF;
            position: absolute;
            bottom: 10%;
            text-shadow: 0px 0 30px #fff;
            font-weight: bold;
            text-align: center;
        }
        /* 添加动画 */
        
        @keyframes rotate {
            0% {
                transform: rotate(0deg);
            }
            100% {
                transform: rotate(360deg);
            }
        }
</style>