<template>
    <div class="mobile-container">
        <form method="get" @submit.prevent="handleSub" id="addF">
            <div class="mobile-search1">
                <select class="mobile-select" name="searchUrl" ref="searchUrl" @change="sel"  v-model="selectValue" id="llq">
                    <option :value="item.href" v-for="item,i in SearchData" :key="i">{{item.title}}</option>
                </select>
            </div>
            <div class="mobile-search2">
                <input type="text" autocomplete="off" class="mobile-input" placeholder="검색어를 입력하세요" v-model="keyWord" name="keyWord" id="inC">
            </div>
            <input type="text" hidden name="test" value="test">
            <input type="submit" class="mobile-submit" value="검색" id="inB">
        </form>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name: "SearchBar",
    data() {
        return {
            SearchTar: "",
            SearchData: [],
            SearchUrl: "",
            keyWord: "",
            selectValue:'https://www.google.co.kr/search?q='
        }
    },
    mounted() {
        axios.get('/api/form.json').then(data=>{
            this.SearchTar = data.data['提交目的链接'];
            this.SearchData = data.data['搜索引擎列表'];
        })
    },
    methods: {
        handleSub(e) {
            let searchUrl = this.$refs.searchUrl.value;
            let keyWord = this.keyWord;
            let url = searchUrl + keyWord;
            window.open(url);
            return false;
        },
        sel(event){
            var llq=document.getElementById("llq");
            var inC=document.getElementById("inC");
            var inB=document.getElementById("inB");
            var addF=document.getElementById("addF");
             this.selectValue = event.target.value; //获取option对应的value值
             if(this.selectValue==="https://www.google.co.kr/search?q="){
                 llq.style.borderColor="#4285f4"
                 inC.style.borderColor="#4285f4"
                 inB.style.borderColor="#4285f4"
                 inB.style.backgroundColor="#4285f4"
                 addF.style.backgroundColor="#4285f4"
             }else if(this.selectValue==="https://search.naver.com/search.naver?sm=top_hty&fbm=1&ie=utf8&query="){
                 llq.style.borderColor="#19cd61"
                 inC.style.borderColor="#19cd61"
                 inB.style.borderColor="#19cd61"
                 inB.style.backgroundColor="#19cd61"
                 addF.style.backgroundColor="#19cd61"
             }else if(this.selectValue==="https://search.daum.net/search?w=tot&DA=YZR&t__nil_searchbox=btn&sug=&sugo=&q="){
                 llq.style.borderColor="#618ffc"
                 inC.style.borderColor="#618ffc"
                 inB.style.borderColor="#618ffc"
                 inB.style.backgroundColor="#618ffc"
                 addF.style.backgroundColor="#618ffc"
             }else if(this.selectValue==="https://search.daum.net/nate?thr=sbma&w=tot&q="){
                 llq.style.borderColor="#ed2000"
                 inC.style.borderColor="#ed2000"
                 inB.style.borderColor="#ed2000"
                 inB.style.backgroundColor="#ed2000"
                 addF.style.backgroundColor="#ed2000"
             }else if(this.selectValue==="http://search.zum.com/search.zum?method=uni&option=accu&rd=1&qm=f_typing.top&query="){
                 llq.style.borderColor="#1b52ed"
                 inC.style.borderColor="#1b52ed"
                 inB.style.borderColor="#1b52ed"
                 inB.style.backgroundColor="#1b52ed"
                 addF.style.backgroundColor="#1b52ed"
             }else if(this.selectValue==="https://www.youtube.com/results?search_query="){
                 llq.style.borderColor="#ff0000"
                 inC.style.borderColor="#ff0000"
                 inB.style.borderColor="#ff0000"
                 inB.style.backgroundColor="#ff0000"
                 addF.style.backgroundColor="#ff0000"
             }else if(this.selectValue==="https://alohafind.com/search?q="){
                 llq.style.borderColor="#666666"
                 inC.style.borderColor="#666666"
                 inB.style.borderColor="#666666"
                 inB.style.backgroundColor="#666666"
                 addF.style.backgroundColor="#666666"
             }
        }
    }
}
</script>
<style scoped>
    .mobile-container {
        width: 100%;
        height: 50px;
        padding: 0;
        margin: 0;
    }
    form {
        position: relative;
        display: block;
        height: 50px;
        background-color: #f0001e;
    }
    .mobile-search1 {
        position: relative;
        display: inline-block;
        width: 25%;
        background-color: #f0001e;
        height: 49px;
        /* top: -21px; */
        box-sizing: border-box;
    }
    .mobile-search2 {
        position: relative;
        display: inline-block;
        width: 55%;
        background-color: #f0001e;
        height: 49px;
        box-sizing: border-box;
    }
    .mobile-select {
        position: relative;
        width: 100%;
        height: 50px;
        border: 5px solid #f0001e;
        box-sizing: border-box;
        padding-left: 20px;
        appearance: none;
        background: url(../assets/img/ar_down.png) no-repeat 90% 50%;
        background-color: white;
        top: -1px;
    }
    .mobile-select:focus-visible, .mobile-input:focus-visible {
        outline: none;
    }
    .mobile-input {
        width: 100%;
        height: 49px;
        border: 4px solid #f0001e;
        box-sizing: border-box;
        padding-left: 20px;
        background-color: white;
        font-size: 12px;
        border-radius: 0;
    }
    select, input {
        margin: 0;
        padding: 0;
    }
    .mobile-submit {
        /* margin-top: 1px; */
        width: 20%;
        height: 50px;
        text-align: center;
        background-color: #f0001e;
        border: none;
        color: white;
        font-weight: 700;
        font-size: 14px;
    }
</style>