            wb5:{},
            wb6:{},
            wb7:{},
            wb8:{},
            wbH1:{},
            wbH2:{},
            wbH3:{},
            wbH4:{},
            wbH5:{},
            wbH6:{},
            wbH7:{},
            wbH8:{},
            footerData: {}
        }
    },
    mounted() {
        axios.get('/api/data.json').then(data=>{
            this.listData = data.data[`推荐1区`];
            this.listHeadData = data.data[`推荐1区头部信息`]
            this.listData2 = data.data[`推荐2区`];
            this.listHeadData2 = data.data[`推荐2区头部信息`]
            this.listData3 = data.data[`推荐3区`];
            this.listHeadData3 = data.data[`推荐3区头部信息`]
            this.listData4 = data.data[`推荐4区`];
            this.listHeadData4 = data.data[`推荐4区头部信息`]
            this.listData5 = data.data[`推荐5区`];
            this.listHeadData5 = data.data[`推荐5区头部信息`]
            this.listData6 = data.data[`推荐6区`];
            this.listHeadData6 = data.data[`推荐6区头部信息`]
            this.listData7 = data.data[`推荐7区`];
            this.listHeadData7 = data.data[`推荐7区头部信息`]
            this.listData8 = data.data[`推荐8区`];
            this.listHeadData8 = data.data[`推荐8区头部信息`]
            this.listData9 = data.data[`推荐9区`];
