            this.listData10 = data.data[`推荐10区`];
            this.listHeadData10 = data.data[`推荐10区头部信息`]
            // 
            this.wb1=data.data[`尾部第一栏`]
            this.wb2=data.data[`尾部第二栏`]
            this.wb3=data.data[`尾部第三栏`]
            this.wb4=data.data[`尾部第四栏`]
            this.wb5=data.data[`尾部第五栏`]
            this.wb6=data.data[`尾部第六栏`]
            this.wb7=data.data[`尾部第七栏`]
            this.wb8=data.data[`尾部第八栏`]
            this.wbH1=data.data[`尾部第1栏头部信息`]
            this.wbH2=data.data[`尾部第2栏头部信息`]
            this.wbH3=data.data[`尾部第3栏头部信息`]
            this.wbH4=data.data[`尾部第4栏头部信息`]
            this.wbH5=data.data[`尾部第5栏头部信息`]
            this.wbH6=data.data[`尾部第6栏头部信息`]
            this.wbH7=data.data[`尾部第7栏头部信息`]
            this.wbH8=data.data[`尾部第8栏头部信息`]
        })
        axios.get('/api/footer.json').then(data => {
            this.footerData = data.data['脚部信息'];
        })
    },
    methods:{
        send(index){
            var i=0;
            if(index===1){
               i=1
           }else if(index===2){
               i=2
           }else if(index===3){
               i=3
           }else if(index===4){
               i=4
           }else if(index===5){
               i=5
           }else if(index===6){
               i=6
           }else if(index===7){
               i=7
           }else if(index===8){
               i=8
           }else if(index===9){
               i=9
           }else if(index===10){
               i=10
           }
           let routeData=this.$router.resolve({
               path:'/h2',
               query:{
                   count:i,
               }
           })
           window.open(routeData.href,'_blank')
        },
        jump(val){
             let routeData=this.$router.resolve({
               path:'/jump',
               query:{
                   name:'top10',
                   address:val
               }
           })
           window.open(routeData.href,'_blank')
        }
    }
}
</script>

