<template>
    <div class="footer-wrap">
        <div class="block1">
            <a :href="footerData.href">
                {{footerData.title}}
            </a>
        </div>
        <div class="block2">
            <p>{{footerData.message1}}</p>
            <p>{{footerData.message2}}</p>
            <p class="gray">{{footerData.message3}}</p>
        </div>
    </div>
</template>
<script>
import axios from 'axios'

export default {
    name: 'MobileFooter',
    data() {
        return {
            footerData: {}
        }
    },
    mounted() {
        axios.get('/api/footer.json').then(data => {
            this.footerData = data.data['脚部信息'];
        });
    },
}
</script>
<style scoped>
    .footer-wrap {
        width: 100%;
        text-align: center;
    }
    .block1 {
        background-color: #f9f9f9;
        height: 60px;
        border: 1px solid #eeeeee;
    }
    .block1 a {
        color: #999999;
        font-size: 13px;
        text-decoration: none;
        line-height: 60px;
    }
    .block2 {
        background-color: #222222;
        color: #ffffff;
        height: 100px;
        padding: 10px;
        font-size: 12px;
    }
    .gray {
        color: #777777;
    }
</style>